import { useMemo } from 'react';
import UAParser from 'ua-parser-js';
import { useSSR } from '../components/ssr-provider';

export function useBrowser() {
  const { isServer } = useSSR();

  if (isServer) {
    return useMemo(
      () => ({
        name: undefined,
        version: undefined,
        major: undefined,
      }),

      [],
    );
  }

  return useMemo(() => {
    const parser = new UAParser();
    return parser.getBrowser();
  }, [navigator.userAgent]);
}
